import { setLocalElementReducer } from "../reducers/localElementReducer";
import { setRemoteElement } from "../reducers/remoteElementReducer";
import { store } from "../redux/store";
import { AxiosInstance } from "../utils/setupAxiosInterceptors";

const ETrackKind = {
    AUDIO: "audio",
    VIDEO: "video",
};

class UserClient {
    constructor(socket, id, username, iskey, roomname) {
        this.socket = socket;
        this.id = id;
        this.username = username;
        this.iskey = iskey;
        this.roomname = roomname;
    }

    produce = async (rtpCapabilities, producerTransport, track, type) => {
        const params = this.getTrackParamsCodec(rtpCapabilities, track);
        const producer = await producerTransport.produce(params);
        producer.type = type;
        producer.on("trackended", () => {
            console.log("producer trackend");
            this.closeProducer(producer);
        });

        producer.on("transportclose", () => {
            console.log("producer transport close");
        });

        producer.on("close", () => {
            console.log("closing producer");
        });
        const producerInstance = {
            kind: producer.kind,
            id: producer.id,
            producer,
        };

        return producerInstance;
    };

    consumer = async (roomname, userId, rtpCapabilities, consumerTransport, producerId) => {
        const consumerData = await AxiosInstance.post("/call/createConsumer", {
            roomname,
            userId,
            consumerParams: {
                rtpCapabilities,
                consumerTransportId: consumerTransport.id, // might be
                producerId,
            },
        });
        const { id, kind, rtpParameters } = consumerData.data.params;
        const codecOptions = [{ ssrc: 222220, scalabilityMode: "S4T1" }];
        const consumer = await consumerTransport.consume({
            id,
            producerId,
            kind,
            rtpParameters,
            codecOptions,
        });

        return consumer;
    };

    getTrackParamsCodec = (rtpCapabilities, track) => {
        switch (track.kind) {
            case ETrackKind.VIDEO: {
                const codec = rtpCapabilities.codecs.find((codec) => codec.mimeType.toLowerCase() === "video/vp8");

                const params = {
                    track,
                    codec,
                    // encodings: [
                    //     { rid: "r0", scalabilityMode: "S1T3" },
                    //     { rid: "r1", scalabilityMode: "S1T3" },
                    //     { rid: "r2", scalabilityMode: "S1T3" },
                    //     { rid: "r3", scalabilityMode: "S1T3" },
                    // ],
                    // codecOptions: {
                    //     videoGoogleStartBitrate: 1000,
                    // },
                };

                return params;
            }
            case ETrackKind.AUDIO: {
                const codec = rtpCapabilities.codecs.find((codec) => codec.mimeType.toLowerCase() === "audio/opus");

                const params = {
                    track,
                    codec,
                };

                return params;
            }

            default:
                break;
        }
    };

    closeProducer = async (producer) => {
        await AxiosInstance.post("/call/closeProducer", {
            roomname: this.roomname,
            userId: this.id,
            producerParams: { producerId: producer.id },
        });
        const localVideoState = { ...store.getState().localElementState };
        if (producer.type === "camera") {
            for (let index = 0; index < localVideoState.videos.length; index++) {
                const element = localVideoState.videos[index];
                if (element.producer.id === producer.id) {
                    localVideoState.videos.splice(index, 1);
                }
            }
        } else if (producer.type === "screenshare") {
            for (let index = 0; index < localVideoState.screenCast.length; index++) {
                const element = localVideoState.screenCast[index];
                if (element.producer.id === producer.id) {
                    localVideoState.screenCast.splice(index, 1);
                }
            }
        } else {
            for (let index = 0; index < localVideoState.audio.length; index++) {
                const element = localVideoState.audio[index];
                if (element.producer.id === producer.id) {
                    localVideoState.audio.splice(index, 1);
                }
            }
        }
        store.dispatch(setLocalElementReducer(localVideoState));
    };

    closeProducerWithType = async (type) => {
        const localVideoState = { ...store.getState().localElementState };
        if (type === "camera") {
            for (let index = 0; index < localVideoState.videos.length; index++) {
                const element = localVideoState.videos[index];
                await AxiosInstance.post("/call/closeProducer", {
                    roomname: this.roomname,
                    userId: this.id,
                    producerParams: { producerId: element.producer.id },
                });
                localVideoState.videos = [];
            }
        } else if (type === "screenshare") {
            for (let index = 0; index < localVideoState.screenCast.length; index++) {
                const element = localVideoState.screenCast[index];
                await AxiosInstance.post("/call/closeProducer", {
                    roomname: this.roomname,
                    userId: this.id,
                    producerParams: { producerId: element.producer.id },
                });
                localVideoState.screenCast = [];
            }
        } else {
            for (let index = 0; index < localVideoState.audio.length; index++) {
                const element = localVideoState.audio[index];
                await AxiosInstance.post("/call/closeProducer", {
                    roomname: this.roomname,
                    userId: this.id,
                    producerParams: { producerId: element.producer.id },
                });
                localVideoState.audio = [];
            }
        }
        store.dispatch(setLocalElementReducer(localVideoState));
    };

    closeConsumer = (consumerId) => {
        const remoteElement = [...store.getState().remoteElementState];
        for (let index = 0; index < remoteElement.length; index++) {
            const element = remoteElement[index];
            if (element.audio?.consumerId === consumerId) {
                element.audio = null;
            } else {
                for (let j = 0; j < element.video.length; j++) {
                    const video = element.video[j];
                    if (video.consumerId === consumerId) {
                        element.video.splice(j, 1);
                    }
                }
            }
        }
        console.log(consumerId, remoteElement);

        store.dispatch(setRemoteElement(remoteElement));
    };

    closeAddConsumerOfUser = (userId) => {
        const remoteElement = [...store.getState().remoteElementState];
        const newRemoteElement = remoteElement.filter((element) => element.peerId !== userId);
        store.dispatch(setRemoteElement(newRemoteElement));
    };
}

export { UserClient };
