import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import Clock from "../Clock/Clock";
import Avatar from "antd/lib/avatar/avatar";

class LargeVideo extends Component {
    // static propTypes = {
    //           prop: PropTypes
    // }

    componentDidMount = () => {
        this.videoRef && (this.videoRef.srcObject = this.props.largeVideoState?.stream);
    };

    componentDidUpdate = (prevProps) => {
        if (prevProps.largeVideoState !== this.props.largeVideoState) {
            this.videoRef && (this.videoRef.srcObject = this.props.largeVideoState?.stream);
        }
    };

    render() {
        console.log(this.props.largeVideoState);
        return (
            <div style={{ position: "relative", width: "100%", height: "100%", background: "black" }}>
                {/* <Clock /> */}
                {this.props.largeVideoState ? (
                    <video
                        autoPlay
                        ref={(el) => {
                            this.videoRef = el;
                        }}
                        style={{ position: "relative", width: "100%", height: "100%", background: "black" }}
                    ></video>
                ) : (
                    <div
                        style={{
                            width: "100%",
                            height: "100%",
                            background: "black",
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center",
                        }}
                    >
                        <img
                            src="./icons/avatar.png"
                            alt=""
                            style={{
                                width: "450px",
                                height: "450px",
                                objectFit: "contain",
                                border: "1px solid #423e3e",
                                borderRadius: "50%",
                            }}
                        />
                    </div>
                )}
            </div>
        );
    }
}
function mapStateToProps(state) {
    return {
        largeVideoState: state.largeVideoState,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        // setModal: (data) => dispatch(setModal(data)),
        // setpdfViewer: (data) => dispatch(setpdfViewer(data)),
    };
}

export default connect(mapStateToProps, null)(LargeVideo);
