import React, { Component } from "react";
import RemoteVideo from "../Components/RemoteVideo/RemoteVideo";
import SliderRight from "../Layouts/SliderRight";
import LargeVideo from "../Components/Video/LargeVideo";
import CameraOption from "../Components/Option/CameraOption";
import SliderLeft from "../Layouts/SliderLeft";
import FooterTool from "../Components/Footer/FooterTool";
import { RoomClient } from "../module/RoomClient";

import { Button, Layout, message, Modal } from "antd";
import { store } from "../redux/store";
import { setRoomCreate } from "../reducers/roomCreateReducer";
import { Role } from "../global/Role";
import { connect } from "react-redux";
import MultipleMediaContainer from "../Components/MutilpleMedia/MultipleMediaContainer";
import Clock from "../Components/Clock/Clock";
import ModalComponent from "../Components/Modal/ModalComponent";
import { setModal } from "../reducers/modalReducer";
import { Input, Row, Tag } from "antd";
import { TwitterOutlined, CopyOutlined } from "@ant-design/icons";
import Chat from "../Components/Chat/Chat";
import { setLocalElementReducer } from "../reducers/localElementReducer";
import { setRemoteElement } from "../reducers/remoteElementReducer";
import { AxiosInstance } from "../utils/setupAxiosInterceptors";
import { setMessage } from "../reducers/messageReducer";
const { Header, Footer, Sider, Content } = Layout;

const mediaType = {
    audio: "audioType",
    video: "videoType",
    screen: "screenType",
};

class Container extends Component {
    constructor(props) {
        super(props);
        this.linkInput = null;
    }

    componentDidMount = async () => {
        if (Role.cameraEnable) {
            await this.onOpenCamera();
        }
        if (Role.micEnable) {
            await this.onOpenMic();
        }
        const roomclient = window.roomClient;
        const localUser = roomclient.localUser;
        const producerInRoomData = await window.roomClient?.getProducerInRoom();
        const producerInRoom = producerInRoomData.data;
        const remoteElementState = [...this.props.remoteElementState];

        for (let index = 0; index < producerInRoom.length; index++) {
            const element = producerInRoom[index];
            if (element.peerId !== localUser.id) {
                const consumer = await localUser.consumer(
                    roomclient.roomname,
                    localUser.id,
                    roomclient.device.rtpCapabilities,
                    roomclient.consumerTransport,
                    element.producerId
                );
                const stream = new MediaStream();
                stream.addTrack(consumer.track);
                const remoteElementIndex = remoteElementState.findIndex((remote) => remote.peerId === element.peerId);
                if (remoteElementIndex !== -1) {
                    if (consumer.track.kind === "video") {
                        remoteElementState[remoteElementIndex].video.push({ stream, consumerId: consumer.id });
                    } else {
                        remoteElementState[remoteElementIndex].audio = { stream, consumerId: consumer.id };
                    }
                } else {
                    if (consumer.track.kind === "video") {
                        remoteElementState.push({
                            peerName: element.peername,
                            peerId: element.peerId,
                            video: [{ stream, consumerId: consumer.id }],
                            audio: null,
                        });
                    } else {
                        remoteElementState.push({
                            peerName: element.peername,
                            peerId: element.peerId,
                            video: [],
                            audio: { stream, consumerId: consumer.id },
                        });
                    }
                }
            }
        }

        await this.props.setRemoteElement(remoteElementState);
        console.log("vao day");
        // window.roomClient.on("stopScreen", async () => {
        //   if (Role.cameraEnable) {
        //     await window.roomClient?.closeProducer(RoomClient.mediaType.video);
        //     window.roomClient?.produce(RoomClient.mediaType.video, true);
        //   }
        //   // if (Role.micEnable) {
        //   //   window.roomClient?.closeProducer(RoomClient.mediaType.audio);
        //   //   window.roomClient?.produce(RoomClient.mediaType.audio, true);
        //   // }
        // });
    };

    onOpenMic = async () => {
        Role.micEnable = true;
        const roomclient = window.roomClient;
        const localUser = roomclient.localUser;
        const mediaConstraints = { video: false, audio: true };
        const mediaDevices = navigator.mediaDevices;
        const stream = await mediaDevices.getUserMedia(mediaConstraints);
        const track = stream.getAudioTracks()[0];
        const producer = await localUser?.produce(
            roomclient.routerCapabilities,
            roomclient.producerTransport,
            track,
            "audio"
        );

        const currentLocalVideoState = { ...this.props.localElementState };
        currentLocalVideoState.audio.push({ producer, stream });
        this.props.setLocalElementReducer(currentLocalVideoState);
    };

    onStopMic = () => {
        Role.micEnable = false;
        const roomclient = window.roomClient;
        const localUser = roomclient.localUser;
        const currentLocalVideoState = { ...this.props.localElementState };
        console.log(currentLocalVideoState);
        for (let index = 0; index < currentLocalVideoState.videos.length; index++) {
            const element = currentLocalVideoState.audio[index];
            const producerInstance = element.producer;
            if (producerInstance.producer.type === "audio") {
                console.log("producerInstance");
                localUser?.closeProducer(producerInstance.producer);
            }
        }
    };

    onOpenCamera = async () => {
        Role.cameraEnable = true;
        const roomclient = window.roomClient;
        const localUser = roomclient.localUser;
        const mediaConstraints = { video: true, audio: false };
        const mediaDevices = navigator.mediaDevices;
        const stream = await mediaDevices.getUserMedia(mediaConstraints);
        const track = stream.getVideoTracks()[0];
        const producer = await localUser?.produce(
            roomclient.routerCapabilities,
            roomclient.producerTransport,
            track,
            "camera"
        );
        const currentLocalVideoState = { ...this.props.localElementState };
        currentLocalVideoState.videos.push({ producer, stream });
        this.props.setLocalElementReducer(currentLocalVideoState);
    };

    onStopCamera = () => {
        Role.cameraEnable = false;
        const roomclient = window.roomClient;
        const localUser = roomclient.localUser;
        const currentLocalVideoState = { ...this.props.localElementState };
        for (let index = 0; index < currentLocalVideoState.videos.length; index++) {
            const element = currentLocalVideoState.videos[index];
            const producerInstance = element.producer;
            console.log(producerInstance.producer);

            if (producerInstance.producer.type === "camera") {
                localUser?.closeProducer(producerInstance.producer);
            }
        }
        console.log(localUser);
        // localUser?.closeProducer(mediaType.video);
    };

    onStartScreenShare = async () => {
        const roomclient = window.roomClient;
        const localUser = roomclient.localUser;
        const mediaConstraints = { video: true, audio: false };
        const mediaDevices = navigator.mediaDevices;
        const stream = await mediaDevices.getDisplayMedia(mediaConstraints);
        const track = stream.getVideoTracks()[0];
        const producer = await localUser?.produce(
            roomclient.routerCapabilities,
            roomclient.producerTransport,
            track,
            "screenshare"
        );
        const currentLocalVideoState = { ...this.props.localElementState };
        currentLocalVideoState.screenCast.push({ producer, stream });
        this.props.setLocalElementReducer(currentLocalVideoState);
    };

    onEndCall = () => {
        window.roomClient?.exit(false);
        store.dispatch(setRoomCreate(false));
    };

    onDisplayChat = async () => {
        const chatElement = document.getElementById("chatElement");
        const display = chatElement.style.display;
        if (display === "none") {
            chatElement.style.display = "flex";
            const messages = await AxiosInstance.post("/call/getMessageInRoom", {
                roomname: window.roomClient.roomname,
            });
            store.dispatch(setMessage(messages.data));
        } else {
            chatElement.style.display = "none";
        }
    };

    onDisplayRemote = () => {
        const chatElement = document.getElementById("remoteVideos");
        const display = chatElement.style.display;
        if (display === "none") {
            chatElement.style.display = "flex";
        } else {
            chatElement.style.display = "none";
        }
    };

    onDisplayRoomInfo = async () => {
        const roomInfo = await AxiosInstance.post("/call/getRoomInfo", {
            roomname: window.roomClient.roomname,
            password: window.roomClient.password,
        });
        this.props.setModal({
            visible: true,
            header: "Thông tin cuộc họp",
            render: (
                <>
                    <Row style={{ margin: "5px 0" }}>
                        <Input
                            addonBefore={
                                <Tag icon={<TwitterOutlined />} color="#55acee">
                                    Tên phòng:
                                </Tag>
                            }
                            placeholder="Nhập tên phòng"
                            disabled
                            value={roomInfo.data.roomname}
                        />
                    </Row>
                    <Row style={{ margin: "5px 0" }}>
                        <Input.Password
                            onChange={(e) => {
                                Role.password = e.target.value;
                            }}
                            addonBefore={
                                <Tag icon={<TwitterOutlined />} color="#cd201f">
                                    Mật khẩu:
                                </Tag>
                            }
                            placeholder="Nhập mật khẩu"
                            value={roomInfo.data.password}
                        />
                    </Row>
                </>
            ),
        });
    };

    onShareLink = () => {
        Modal.info({
            title: "Liên kết chia sẻ",
            content: (
                <div>
                    <Input.Search
                        id="copyLink"
                        onSearch={(value) => {
                            document.getElementById("copyLink").select();
                            document.execCommand("copy");
                            message.info(value);
                        }}
                        allowClear
                        enterButton={<Button data-clipboard-target="#copyLink" icon={<CopyOutlined />}></Button>}
                        value={window.location.href}
                    ></Input.Search>
                </div>
            ),
            footer: null,
        });
    };

    render() {
        const { viewOneCamera, remoteElementState } = this.props;
        console.log("remoteElementState", remoteElementState);
        return (
            <Layout style={{ height: "100%" }}>
                <ModalComponent />
                <Sider style={{ display: "none" }} id="chatElement" width={350} onClick={this.onCreateRoom}>
                    <Chat />
                </Sider>
                <Layout>
                    {viewOneCamera ? (
                        <Content id="localMedia">
                            <Layout style={{ height: "100%" }}>
                                <Sider
                                    width={150}
                                    id="localVideos"
                                    style={{ position: "absolute", zIndex: 2, background: "transparent" }}
                                >
                                    <SliderLeft />
                                </Sider>
                                <Content style={{ overflow: "hidden" }} id="localMedia">
                                    <LargeVideo />
                                </Content>
                            </Layout>
                        </Content>
                    ) : (
                        <Content id="multipleMedia">
                            <Layout style={{ height: "100%" }}>
                                <MultipleMediaContainer />
                            </Layout>
                        </Content>
                    )}

                    <Footer>
                        <FooterTool
                            onStopMic={this.onStopMic}
                            onOpenMic={this.onOpenMic}
                            onDisplayChat={this.onDisplayChat}
                            onStartScreenShare={this.onStartScreenShare}
                            onEndCall={this.onEndCall}
                            onStopCamera={this.onStopCamera}
                            onOpenCamera={this.onOpenCamera}
                            onDisplayRemote={this.onDisplayRemote}
                            onDisplayRoomInfo={this.onDisplayRoomInfo}
                            onShareLink={this.onShareLink}
                        />
                    </Footer>
                </Layout>
                {viewOneCamera ? (
                    <Sider
                        style={{
                            overflowX: "hidden",
                            overflowY: "auto",
                            maxHeight: "100%",
                            position: "absolute",
                            right: 0,
                            top: 0,
                            background: "transparent",
                            display: remoteElementState.length > 0 ? "block" : "none",
                        }}
                        width={150}
                        id="remoteVideos"
                    >
                        <SliderRight />
                    </Sider>
                ) : null}
            </Layout>
        );
    }
}
function mapStateToProps(state) {
    return {
        viewOneCamera: state.ui.viewOneCamera,
        remoteElementState: state.remoteElementState,
        localElementState: state.localElementState,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        setModal: (data) => dispatch(setModal(data)),
        setLocalElementReducer: (data) => dispatch(setLocalElementReducer(data)),
        setRemoteElement: (data) => dispatch(setRemoteElement(data)),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(Container);
