const TIMEOUT = 1 * 60 * 100000;

let AxiosInstance;
const setupAxiosInterceptors = (onUnauthenticated) => {
    AxiosInstance = Axios.create({
        timeout: TIMEOUT,
        baseURL: `${window.location.protocol}//${window.serverIp}:${window.port}`
    });

    const onRequestSuccess = (config) => {
        //run with authen

        // const token = getToken("token");
        // if (token) {
        //     config.headers.Authorization = `Bearer ${token}`;
        // }

        return config;
    };
    const onResponseSuccess = (response) => response?.data;

    const onResponseError = (err) => {
        //run with authen

        // const status = err.status || (err.response ? err.response.status : 0);
        // if (status === 403 || status === 401) {
        //     clearAll();
        //     store.dispatch(setAuth(false));
        // } else {
        //     store.dispatch(
        //         setErrorBoundry({
        //             show: true,
        //             error: `From: ${err.config.url}\n` + err,
        //         })
        //     );
        // }

        //run without authen
        // store.dispatch(
        //     setErrorBoundry({
        //         show: true,
        //         error: `From: ${err.config.url}\n` + err,
        //     })
        // );
        return Promise.reject(err);
    };

    AxiosInstance.interceptors.request.use(onRequestSuccess);
    AxiosInstance.interceptors.response.use(onResponseSuccess, onResponseError);
};

export { setupAxiosInterceptors, AxiosInstance };
