import { Button, Input, Form } from "antd";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Role } from "../global/Role";
import Container from "../Layouts/Container";
import { RoomClient } from "../module/RoomClientNew";
import { setRoomCreate } from "../reducers/roomCreateReducer";
import { Base64 } from "../utils/base64";

class HomePage extends Component {
    constructor(props) {
        super(props);
        this.state = { micEnable: false, cameraEnable: true };
    }

    componentDidMount = () => {
        const url = new URL(window.location.href);
        let params = url.searchParams.get("option");
        if (params) {
            const paramsDecode = Base64.decode(params);
            let objJsonStr = JSON.parse(paramsDecode);
            this.formRef.setFieldsValue({
                roomName: objJsonStr.roomName,
                displayName: objJsonStr.username,
                password: objJsonStr.roomKey,
            });
        }
    };

    onFinish = (form) => {
        Role.micEnable = this.state.micEnable;
        Role.cameraEnable = this.state.cameraEnable;
        const { roomName, displayName, password } = form;
        this.onCreateRoom(roomName, displayName, password);
    };

    onCreateRoom = (roomName, displayName, password) => {
        const roomClient = new RoomClient(roomName, password, displayName, async (roomClient) => {
            await roomClient.init();
            await roomClient.initSocketEvent();
            window.roomClient = roomClient;
            this.props.setRoomCreate(true);
        });
    };
    render() {
        return (
            <div
                style={{
                    display: "flex",
                    width: "100%",
                    height: "100%",
                    alignItems: "center",
                    justifyContent: "center",
                }}
            >
                {this.props.roomCreateState ? (
                    <Container />
                ) : (
                    <div>
                        <Form ref={(ref) => (this.formRef = ref)} name="control-ref" onFinish={this.onFinish}>
                            <Form.Item name="roomName" label="Tên cuộc họp" rules={[{ required: true }]}>
                                <Input />
                            </Form.Item>
                            <Form.Item name="displayName" label="Tên hiển thị" rules={[{ required: true }]}>
                                <Input />
                            </Form.Item>
                            <Form.Item name="password" label="Mật khẩu cuộc họp">
                                <Input />
                            </Form.Item>
                            <Form.Item>
                                <Button
                                    onClick={() => {
                                        this.setState({ micEnable: !this.state.micEnable });
                                    }}
                                    icon={
                                        !this.state.micEnable ? (
                                            <i className="material-icons">mic_off</i>
                                        ) : (
                                            <i className="material-icons">mic</i>
                                        )
                                    }
                                ></Button>
                                <Button
                                    onClick={() => {
                                        this.setState({ cameraEnable: !this.state.cameraEnable });
                                    }}
                                    icon={
                                        !this.state.cameraEnable ? (
                                            <i className="material-icons">videocam_off</i>
                                        ) : (
                                            <i className="material-icons">videocam</i>
                                        )
                                    }
                                ></Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="submit">
                                    Tạo cuộc họp/Tham gia vào cuộc họp
                                </Button>
                            </Form.Item>
                        </Form>
                    </div>
                )}
            </div>
        );
    }
}
function mapStateToProps(state) {
    return {
        roomCreateState: state.roomCreateState,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        setRoomCreate: (data) => dispatch(setRoomCreate(data)),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(HomePage);
