import { Input, Modal, Row, Tag } from "antd";
import React, { Component } from "react";
import { TwitterOutlined, YoutubeOutlined, FacebookOutlined, LinkedinOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import { setModal } from "../../reducers/modalReducer";
import { Role } from "../../global/Role";
import { AxiosInstance } from "../../utils/setupAxiosInterceptors";
import { Notification } from "../../module/Notification";

class ModalComponent extends Component {
    handleCancel = (e) => {
        this.props.setModal({ visible: false, header: "", render: null });
    };
    onOk = async () => {
        const res = await AxiosInstance.post("/call/changeRoomPassword", {
            roomname: window.roomClient.roomname,
            password: Role.password,
        });
        Notification("info", res.message);
    };

    render() {
        console.log(this.props.modalState);
        return (
            <Modal
                title={this.props.modalState.header}
                visible={this.props.modalState.visible}
                onCancel={this.handleCancel}
                onOk={this.onOk}
                closable={false}
            >
                {this.props.modalState.render}
            </Modal>
        );
    }
}
function mapStateToProps(state) {
    return {
        modalState: state.modalState,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        setModal: (data) => dispatch(setModal(data)),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(ModalComponent);
