import React, { Component } from "react";
import PropTypes from "prop-types";
import "./style.scss";
import { RoomClient } from "../../module/RoomClient";
import { store } from "../../redux/store";
import { setLocalElementReducer } from "../../reducers/localElementReducer";

export default class CameraOption extends Component {
    // static propTypes = {
    //           prop: PropTypes
    // }

    constructor(props) {
        super(props);
        this.state = { video: [] };
    }

    componentDidMount = async () => {
        const devices = await window.navigator.mediaDevices.enumerateDevices();
        const video = [];
        for (let index = 0; index < devices.length; index++) {
            const deviceInfo = devices[index];
            if (deviceInfo.kind === "videoinput") {
                video.push({ deviceInfo, active: false });
            }
        }
        this.setState({ video });
    };

    onActiveOptionCamera = async (item, index) => {
        const roomclient = window.roomClient;
        const localUser = roomclient.localUser;
        const currentVideo = this.state.video;
        for (let i = 0; i < currentVideo.length; i++) {
            const element = currentVideo[i];
            if (i === index) {
                element.active = true;
                localUser.closeProducerWithType("camera");
                const mediaConstraints = { video: { deviceId: element.deviceInfo.deviceId }, audio: false };
                const mediaDevices = navigator.mediaDevices;
                const stream = await mediaDevices.getUserMedia(mediaConstraints);
                const track = stream.getVideoTracks()[0];
                const producer = await localUser?.produce(
                    roomclient.routerCapabilities,
                    roomclient.producerTransport,
                    track,
                    "camera"
                );
                const currentLocalVideoState = { ...store.getState().localElementState };
                currentLocalVideoState.videos.push({ producer, stream });
                store.dispatch(setLocalElementReducer(currentLocalVideoState));
            } else {
                element.active = false;
            }
        }
        this.setState({ video: currentVideo });
    };

    render() {
        return (
            <div className="micOption" key="camera">
                <div className="row">
                    <i className="material-icons"> videocam</i>
                    <h2>Videos</h2>
                </div>
                {this.state.video.map((item, index) => {
                    return (
                        <div onClick={() => this.onActiveOptionCamera(item, index)} key={"camera" + index}>
                            {" "}
                            <div className="row padding">
                                {item.active ? (
                                    <i className="material-icons"> check</i>
                                ) : (
                                    <i className="material-icons"> videocam</i>
                                )}
                                <span>{item.deviceInfo.label}</span>
                            </div>
                        </div>
                    );
                })}
            </div>
        );
    }
}
